import React from 'react';
import { css } from '@emotion/core';
import { animated, useTransition, useKeyframes } from 'react-spring/hooks';
import Container from '../styled/Container';

const Hero = ({ heading, subheading }) => {
  const keyframes = useKeyframes.spring({
    config: { tension: 80, friction: 20 },
    wiggle: async next => {
      while (true) {
        await next({ transform: 'translate3d(0, 0.5rem, 0' });
        await next({ transform: 'translate3d(0, 0, 0' });
      }
    },
  });

  const h2Transition = useTransition({
    items: [<h2 css={headingCSS}>{heading}</h2>],
    keys: [0],
    from: { opacity: 0, transform: 'translateY(-10px)' },
    enter: { opacity: 1, transform: 'translateY(0)' },
    leave: { opacity: 0, transform: 'translateY(10px)' },
    delay: 500,
  });

  const pTransition = useTransition({
    items: [<p css={copyCSS}>{subheading}</p>],
    keys: [0],
    from: { opacity: 0, transform: 'translateY(-10px)' },
    enter: { opacity: 1, transform: 'translateY(0)' },
    leave: { opacity: 0, transform: 'translateY(10px)' },
    delay: 1250,
  });

  const learnProps = keyframes('wiggle');

  return (
    <section css={baseCSS}>
      <Container>
        {h2Transition.map(({ item, key, props }) => (
          <animated.div style={props} key={key}>
            {item}
          </animated.div>
        ))}
        {pTransition.map(({ item, key, props }) => (
          <animated.div style={props} key={key}>
            {item}
          </animated.div>
        ))}
      </Container>
      <animated.span css={learnMoreCSS} style={learnProps}>
        Learn More
        <svg
          css={learnMoreIconCSS}
          height="13"
          viewBox="0 0 14 13"
          width="14"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="m13.6674505 5.37353066-.6738172-.65745638c-.2335022-.21621481-.5062205-.32435292-.8175253-.32435292-.3177255 0-.587202.10810741-.8088702.32435292l-2.64182651 2.57723021v-6.17117971c0-.30396572-.11396571-.56691142-.34164535-.78899063-.22767963-.22204849-.49718761-.33313415-.80864983-.33313415h-1.15020075c-.31149369 0-.58112757.11117777-.80864983.33313415-.22774259.22207921-.34148798.48502491-.34148798.78899063v6.17117971l-2.64185802-2.57723021c-.22163671-.21621481-.49117617-.32435292-.80877572-.32435292-.31146222 0-.58411755.10810741-.81765127.32435292l-.66497309.65745638c-.22774258.22189497-.34151945.48800315-.34151945.79764904 0 .31569451.11390277.57870162.34142503.78902134l5.84979924 5.71535396c.22173114.2163376.49127059.324445.80877573.324445.31149369 0 .58408607-.1081074.81758831-.324445l5.84983069-5.71535396c.2216682-.21624552.332581-.47922193.332581-.78902134.0000315-.3038122-.1108813-.56970545-.3325495-.79764904z"
            fill="#fff"
          />
        </svg>
      </animated.span>
      <svg
        css={cutoutCSS}
        viewBox="0 0 1280 75"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path d="M 0,0 Q 320,75 640,75 L 0,75 Z" />
        <path d="M 640,75 Q 960,75 1280,0 L 1280,75 Z" />
        <line x1="0" y1="75" x2="1280" y2="75" stroke="#fff" />
      </svg>
    </section>
  );
};

export default Hero;

const baseCSS = css`
  align-items: center;
  background: linear-gradient(to bottom, #3399ff, #0080ff);
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 90vh;
  justify-content: center;
  position: relative;
  text-align: center;
`;

const headingCSS = css`
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 2.5rem;
`;

const copyCSS = css`
  font-size: 1.375rem;
  line-height: 150%;
  margin: 0 auto 4.5rem;
  max-width: 36.5rem;
  width: 90%;
`;

const learnMoreCSS = css`
  align-items: center;
  bottom: 2rem;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-size: 0.75rem;
  font-weight: 700;
  position: absolute;
  text-decoration: none;
`;

const learnMoreIconCSS = css`
  height: 0.8125rem;
  margin-top: 0.125rem;
  width: 0.875rem;
`;

const cutoutCSS = css`
  bottom: 0;
  fill: #fff;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  width: 100%;
`;
